<template>
  <div class="flex h-full lg:flex-col justify-center lg:items-center">
    <div class="w-full lg:max-w-4xl max-w-xl">
      <transition name="slide-fade">
        <template v-if="isVerifyEmail">
          <VerifyEmail />
        </template>
        <template v-else>
          <div>
            <div
              v-if="!showExistAccountOption"
              class="w-full lg:block flex flex-col items-center justify-center lg:w-3/4 xl:w-3/4">
              <h2 class="text-3xl font-semibold">Start your free trial</h2>
              <p whitelabel="text-replace" data-label="trial period">
                14-day trial &bull; No credit card required &bull; Access all
                features
              </p>
              <form @submit.prevent="handleSubmit" autocomplete="on">
                <div class="my-8">
                  <div class="my-4">
                    <Input
                      v-model="data.name"
                      autocomplete="name"
                      label="Name"
                      block
                      :view="validation.name.$invalid ? 'danger' : ''"
                    />
                    <small
                      class="text-red-base block mt-2"
                      v-for="(error, errorIndex) of validation.name.$errors"
                      :key="errorIndex"
                      >{{ error.$message }}.
                    </small>
                  </div>
                  <div class="my-4">
                    <Input
                      v-model="data.email"
                      autocomplete="email"
                      label="Email Address"
                      block
                      :view="validation.email.$invalid ? 'danger' : ''"
                    />
                    <small
                      class="text-red-base block mt-2"
                      v-for="(error, errorIndex) of validation.email.$errors"
                      :key="errorIndex"
                      >{{ error.$message }}.
                    </small>
                  </div>
                  <div class="my-4">
                    <Input
                      v-model="data.password"
                      autocomplete="new-password"
                      :type="isShowPassword ? 'text' : 'password'"
                      label="Password"
                      block
                      :view="validation.password.$invalid ? 'danger' : ''"
                      icon
                      right
                    >
                      <template #icon>
                        <i
                          @click="isShowPassword = !isShowPassword"
                          class="material-icons select-none cursor-pointer"
                          >{{
                            isShowPassword ? 'visibility_off' : 'visibility'
                          }}</i
                        >
                      </template>
                    </Input>
                    <small
                      class="text-red-base block mt-2"
                      v-for="(error, errorIndex) of validation.password.$errors"
                      :key="errorIndex"
                      >{{ error.$message }}.
                    </small>
                  </div>
                </div>
                <div v-if="isRegisteredReady && !showExistAccountOption" class="bg-blue-01 bg-opacity-30 rounded-md p-4 text-blue-03">
                  <div class="flex gap-x-2">
                    <span class="material-icons text-4xl">info</span>
                    <p class="text-base leading-7">
                      This email is already associated with an account, but the password you entered is incorrect, Please try again or
                      <a
                        href="/forgot-password"
                        class="hover:underline text-grey-darken">
                        reset password
                      </a>
                      if you've forgetten it.
                    </p>
                  </div>
                </div>
                <div whitelabel="remove" class="my-8">
                  <p>
                    By signing up, you agree to our
                    <a
                      href="https://www.reusely.com/privacy-policy"
                      target="_blank"
                      rel="noreferrer noopener"
                      >Privacy Policy</a
                    >
                    and
                    <a
                      href="https://www.reusely.com/terms-of-service"
                      target="_blank"
                      rel="noreferrer noopener"
                      >Terms & Conditions</a
                    >
                  </p>
                </div>
                <Button
                  :loading="loading"
                  color="teal"
                  :disabled="isIncompleteForm"
                  >Continue <i class="material-icons ml-2">east</i></Button
                >
              </form>
            </div>
            <!-- OPTION FOR ALREADY ACCOUNT -->
            <div
              v-else>
              <form
                class="space-y-8"
                @submit.prevent="handleActionExistAccount">
                <p class="text-3xl text-black font-semibold">
                  An account with this email address already exists
                </p>
                <div class="grid grid-cols-6 gap-4 sm:h-80">
                  <div class="lg:col-span-2 sm:col-span-3 col-span-6">
                    <button
                      :class="[
                        'p-6 rounded-xl bg-white',
                        'center justify-center gap-4 border',
                        'flex h-full w-full flex-col items-center',
                        'duration-300 transition-all ease-in-out',
                        selectedAction === 1
                          ? 'border-teal-04 shadow'
                          : 'border-opacity-10 border-teal-02 hover:border-teal-04 hover:shadow'
                      ]"
                      type="button"
                      @click="selectedAction = 1">
                      <div
                        class="h-32 w-32 bg-grey-03"
                        :style="{
                          maskImage: `url('/icon/onboarding/change-email.svg')`,
                          maskSize: 'contain',
                          maskPosition: 'center',
                          maskRepeat: 'no-repeat'
                        }"
                      />
                      <p class="text-teal-04 font-semibold text-xl">Use Different Email</p>
                      <p class="text-sm">Let's try a different email address</p>
                    </button>
                  </div>
                  <div class="lg:col-span-2 sm:col-span-3 col-span-6">
                    <button
                      :class="[
                        'p-6 rounded-xl bg-white',
                        'flex h-full w-full flex-col items-center',
                        'center justify-center gap-4 border',
                        'duration-300 transition-all ease-in-out',
                        selectedAction === 2
                          ? 'border-teal-04 shadow'
                          : 'border-opacity-10 border-teal-02 hover:border-teal-04 hover:shadow'
                      ]"
                      type="button"
                      @click="selectedAction = 2">
                      <div
                        class="h-32 w-32 bg-grey-03"
                        :style="{
                          maskImage: `url('/icon/onboarding/add-new-account.svg')`,
                          maskSize: 'contain',
                          maskPosition: 'center',
                          maskRepeat: 'no-repeat'
                        }"
                      />
                      <p class="text-teal-04 font-semibold text-xl">Add New Account</p>
                      <p class="text-sm">Create an addtional account with the same email address</p>
                    </button>
                  </div>
                </div>
                <Button
                  color="teal"
                  type="submit">
                  Continue
                </Button>
              </form>
            </div>
            <!-- OPTION FOR ALREADY ACCOUNT -->
          </div>
        </template>
      </transition>
    </div>
  </div>
</template>
<script>
import { defineComponent, onMounted, computed, ref } from 'vue'
import { useRouter, useRoute } from 'vue-router'

// Composables
import { usePersonalInformation } from 'composable/onboarding'
import { useLogin } from 'composable/user'


// Components
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'

import VerifyEmail from './verify-email/index.vue'

// Utils
import { useVuelidate } from '@vuelidate/core'
import { initNewSession } from 'utils/session'

export default defineComponent({
  components: {
    Input,
    Button,
    VerifyEmail
  },
  async setup() {
    const router = useRouter()
    const route = useRoute()
    const {
      data,
      loading,
      isShowPassword,
      isRegisteredReady,
      validationRules,
      action,
      isIncompleteForm,
      isVerifyEmail
    } = usePersonalInformation()
    const {
      data: _loginData,
      login: _loginAction
    } = useLogin()
    const selectedAction = ref(0)
    const showExistAccountOption = ref(false)

    const validation = useVuelidate(validationRules, data, {
      $lazy: true
    })
    const validate = () => {
      if (isIncompleteForm.value) return false
      validation.value.$validate()
      return !validation.value.$invalid
    }
    const handleSubmit = async () => {
      const isValid = validate()
      if (isValid) {
        isRegisteredReady.value = false

				window.localStorage.setItem('region_domain', import.meta.env.VITE_BASE_URL_API_US)
				window.localStorage.setItem('region_socket', import.meta.env.VITE_SOCKET_URL_US)

				await action(async (response) => {
					if (!!window?.growsumo) {
						window.growsumo.data.name = data.name
						window.growsumo.data.email = data.email
						window.growsumo.data.customer_key = data.email
						window.growsumo.createSignup();
					}


          const { code, company_token, verify_status } = response
          if (verify_status) {
            if (verify_status == 1) isRegisteredReady.value = true
            if (verify_status == 2) showExistAccountOption.value = true
          } else {
            if (code && company_token) {
              await router.push({
                name: 'onboardingPersonalInformation',
                query: {
                  token: btoa(
                    JSON.stringify({
                      email: data.email,
                      code_encrypted: code,
                      company_token
                    })
                  )
                }
              })
            }
          }
        }).catch((error) => {
					window.localStorage.removeItem('region_domain')
					window.localStorage.removeItem('region_socket')
				})
      }
    }
    const handleActionExistAccount = async () => {
      if (selectedAction.value === 1) {
        isRegisteredReady.value = false
        showExistAccountOption.value = false
        data.name = ''
        data.email = ''
        data.password = ''
      } else {
        _loginData.email = data.email
        _loginData.password = data.password
        _loginAction(res => {
          if (!!res.errors) return

					if (res.re_login){
						window.localStorage.setItem('region_domain', res.domain)
						window.localStorage.setItem('region_socket', res.socket)

						loginSubmit()

						return
					}

          window.localStorage.setItem('token', res.token)
          initNewSession()
          router.push({
            path: '/new-company/1',
          })
        })
      }
    }


    return {
      data,
      loading,
      isShowPassword,
      isRegisteredReady,
      isIncompleteForm,
      isVerifyEmail,
      validation,
      handleSubmit,
      selectedAction,
      showExistAccountOption,
      handleActionExistAccount
    }
  }
})
</script>
