<template>
  <div class="mb-8 mx-auto w-full md:w-4/5 lg:w-full xl:mx-0 xl:w-2/3">
    <Transition name="slide-fade">
      <div v-if="loading || isSetupAccount">
        <div class="w-full lg:w-2/3 my-20">
          <h2 class="text-3xl font-semibold">
            We're now setting up your account...
          </h2>
          <div class="flex items-center justify-center w-full my-20">
            <BaseLoading />
          </div>
          <p class="text-sm">
            Please wait for us to verify your information to grant you access to
            your free trial. This may take a moment. Please do not refresh or
            navigate away from this page
          </p>
        </div>
      </div>
      <div v-else>
        <h2 class="text-3xl font-semibold">Verify Email Address</h2>
        <p>
          you will receive an activation code within the next few minutes to
          <span class="font-semibold">{{ decodedTokenPayload.email }}</span>
        </p>
        <form @submit.prevent="handleVerify">
          <div class="my-8 w-full">
            <InputVerificationCode v-model="data.code" />
          </div>
          <div class="my-10">
            <Button
              :disabled="isIncomplete"
              color="teal"
              :loading="loading"
              >Continue <i class="material-icons ml-2">east</i></Button
            >
          </div>
        </form>
      </div>
    </Transition>
  </div>
</template>
<script>
import { defineComponent, ref, onMounted } from 'vue'
import { useRouter, useRoute } from 'vue-router'

// Composables
import { useEmailVerification } from 'composable/onboarding'

// Components
import Button from 'bbUi/components/button/button.vue'
import BaseLoading from 'components/BaseLoading.vue'

import InputVerificationCode from 'components/InputVerificationCode.vue'

export default defineComponent({
  components: {
    Button,
    InputVerificationCode,
    BaseLoading
  },
  setup() {
    const router = useRouter()
    const route = useRoute()
    const { data, loading, isIncomplete, decodedTokenPayload, action } =
      useEmailVerification()
    const isSetupAccount = ref(false)

    const handleVerify = async () => {
      if (!isIncomplete.value) {
        isSetupAccount.value = true
        await action(async (err) => {
          if (err) {
            isSetupAccount.value = false
            return
          }
          await router.push({
            name: 'onboardingPurchasingMethod',
            params: {
              verified: true // this need to be set to true when navigating away from verify email section. See validation #86792nrh4
            }
          })
        })
      }
    }

    onMounted(async () => {
      const { code_encrypted, company_token } = decodedTokenPayload.value
      data.code_encrypted = code_encrypted || null
      data.company_token = company_token || null
    })

    return {
      route,
      data,
      loading,
      isIncomplete,
      isSetupAccount,
      decodedTokenPayload,
      handleVerify
    }
  }
})
</script>
